<template>
<!-- 订单基本信息 -->
  <div class="order-base">
          
    <div class="item clearfix">
        <div class="left">
            <div class="item-left">
                <span>下单时间</span>
            </div>
            <div class="item-right">
                <span>{{ baseinfo.order_time  }}</span>
            </div>
        </div>

        <div class="left">
            <div class="item-left">
                <span>流水号</span>
            </div>
            <div class="item-right">
                <span>{{ baseinfo.order_water_num }}</span>
            </div>
        </div>
    </div>

    <div class="item clearfix">
        <div class="left">
            <div class="item-left">
                <span>餐牌号</span>
            </div>
            <div class="item-right">
                <span>{{ baseinfo.seat_name  }}</span>
            </div>
        </div>
        <div class="left">
            <div class="item-left">
                <span>用餐人数</span>
            </div>
            <div class="item-right">
                <span>{{ baseinfo.customer_num  }}</span>
            </div>
        </div>
    </div>

    <div class="item clearfix">
        <div class="left">
            <div class="item-left">
                <span>餐桌区域</span>
            </div>
            <div class="item-right">
                <span>{{ baseinfo.seat_region  }}</span>
            </div>
        </div>
        <div class="left">
            <div class="item-left">
                <span>销售方式</span>
            </div>
            <div class="item-right">
                <span>{{ foodSaleWay.toString(baseinfo.dine_way)  }}</span>
            </div>
        </div>
    </div>

    <div class="item clearfix">
        <div class="left">
            <div class="item-left">
                <span>餐桌类型</span>
            </div>
            <div class="item-right">
                <span>{{ baseinfo.seat_type  }}</span>
            </div>
        </div>
        <div class="left">
            <div class="item-left">
                <span>订单状态</span>
            </div>
            <div class="item-right">
                <span>{{ OrderListStatue.toString(baseinfo.order_status)  }}</span>
            </div>
        </div>
    </div>

    <div class="item clearfix">
        <div class="left">
            <div class="item-left">
                <span>是否开票</span>
            </div>
            <div class="item-right">
                <span>{{ IsInvoice.toString(baseinfo.is_invoicing)  }}</span>
            </div>
        </div>
    </div>
          
  </div>
</template>

<script>
import {
  foodSaleWay,
  formPayWay,
  IsInvoice,
  OrderListStatue
} from "@/config/cfg";

export default {
  props: {
    baseinfo: {
      type: Object
    }
  },
  data() {
    return {
      foodSaleWay: foodSaleWay,
      formPayWay: formPayWay,
      IsInvoice: IsInvoice,
      OrderListStatue: OrderListStatue
    };
  }
};
</script>

<style lang="scss" scoped>
@import "src/styles/mixin.scss";
.order-base {
  border: 1px solid #d8d8d8;
  padding-top: 20px;
  width: 100%;
}
.item {
  margin-bottom: 20px;
  div {
    width: 50%;

    .item-left {
      @include fc(14px,#000);
      display: inline-block;
      width: 106px;
      opacity: 0.5708;
      text-align: right;
    }
    .item-right {
      @include fc(14px,#333);
      display: inline-block;
      padding-left: 14px;
    }
  }
}
</style>



